<template>
    <div >
      <el-menu
        :default-active="activeIndex"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
        style="overflow: hidden"
      >
        <el-menu-item  @click="gohome()" style="width:13%;text-align: center" index="0">首页</el-menu-item>
        <el-submenu  style="margin-left: 20px;width: 15%;text-align: center"
         :index="(index+1).toString()"  v-for="(item,index) in this.$store.state.menuInf" :key="index">
          <template slot="title" >{{item.menuLabel}}</template>
          <!--这里Index必须是字符串类型 所以需要转换成字符串-->
          <!--Index必须+因为element-ui样式是 1-1 不是 0-1-->
          <el-menu-item ref="elmenuitem" style="width: 100%"
                        :index="((index*1+1) +'-'+(index2*1+1)).toString()"
                        @click="mybtn(index,index2,item2)"
                        v-for="(item2,index2) in item.submenu"
                        :key="index2" >{{item2.menuLabel}}</el-menu-item>
        </el-submenu>
        <!--登出功能-->
        <el-button  @click="logout(tagName)" style="margin-top: 10px; margin-right: 20px; float: right" type="danger" round>注销</el-button>
      </el-menu>
    </div>
</template>

<script>
  import common from '../api/common'
  import axios from 'axios'
  export default {
    data(){
      return{
        activeIndex: '1',
        activeIndex2: '1',
        loginOuturl:common.loginOut,
        sessionId :sessionStorage.getItem("sessionID")
      }
    },
    components:{

    },
    methods: {
//     handleSelect(key, keyPath) {
//       console.log(key, keyPath);
// },
      gohome(){
        this.$router.push({
          path:"/Home/firstpage/",

        })
      },
      handleSelect(key, keyPath) {

      },
      mybtn(index,index2,item2){
        // console.log(item2.menuLabel);
        this.$router.push({
          //子路由跳转
          path:"/Home/"+item2.menuUrl+"/",
          // query:{//通过query 传递参数
          //   "name":item2.menuLabel,
          // }
        })
      },
      //登出功能
      logout(){
        axios.post(this.loginOuturl,this.sessionId).then(response=>{
          sessionStorage.removeItem('sessionID');
          this.$router.push({
            path:'/'
          })
        }).catch({

        })
      },
    },

  }
</script>

<style scoped>

</style>



