<template>
  <div>
    <div style="margin-top: 20px">
      <!--弹出框-->
      <el-button @click="dialogTableVisible = true" slot="reference"   style="float: right; margin-right:200px" type="success" plain>创建订单</el-button>
      <el-dialog style="text-align: center;" :fullscreen='true' :visible.sync="dialogTableVisible">
        <div style="margin-bottom: 50px">
          <span style="font-size: 28px;font-weight: 400">课程信息</span>
        </div>
        <el-table
          @click="mybtn(index)"
          ref="singleTable"
          :data="this.course"
          highlight-current-row
          @current-change="handleCurrentChange"
          style=" width: 50%;
text-align: center;margin:  0 auto">
          <el-table-column
            ref="singleTables"
            type="index"
            width="50"
          style="margin-left: 30px">
          </el-table-column>
          <el-table-column
            property="course_name"
            label="课程名称"
            width="120"
            style="margin-left: 30px">
          </el-table-column>
          <el-table-column
            property="begin_date"
            label="起始日期"
            width="120"
            style="margin-left: 30px">
          </el-table-column>
          <el-table-column
            property="end_date"
            label="结束日期"
            width="120"
            style="margin-left: 30px">
          </el-table-column>
          <el-table-column
            property="lecturer_name"
            label="导师"
            width="120"
            style="margin-left: 30px">
          </el-table-column>
          <el-table-column
            property="price"
            label="价格"
            style="margin-left: 30px">
          </el-table-column>
          <!--<el-table-column-->
            <!--property="course_id"-->
            <!--label="course_id"-->
          <!--style="display: none">-->
          <!--</el-table-column>-->
          <!--<el-table-column-->
            <!--property="prodline_id"-->
            <!--label="prodline_id">-->
          <!--</el-table-column>-->
        </el-table>
        <div style="margin-top: 20px">
          <el-button @click="dialogTableVisible = false">取 消</el-button>
          <el-button type="primary" @click="nexthome">下一步</el-button>
          <el-dialog :modal="false" style="text-align: center" :fullscreen='true' title="课程信息" :visible.sync="dialogFormVisible">
            <!--<el-button @click=" addDomain">增加</el-button>-->
            <div  style="font-size: 30px; font-weight: 600;display: inline-block; " >{{this.title}}</div>
            <div  style=" margin-bottom:50px;margin-left: 50px; font-size: 25px; font-weight: 400;margin-top: -20px;display: inline-block">标准价格￥{{this.myprice}}</div>
            <!--已有表单-->
            <el-form :model="ruleform">
              <!--循环增加表单-->
              <div style="text-align: center">
                <div style="width: 100%" class="moreRulesIn" v-for="(item, index) in ruleform.moreNotifyObject" :key="item.key">
                  <span >姓名:</span>
                  <el-form-item style="display: inline-block;width: 250px;margin-left: 10px" class="rules"  :prop="'moreNotifyObject.' + index +'.username'" :rules="rules.userName" >
                    <el-input v-model="item.username" placeholder="请输入姓名"  class="el-select_box"></el-input>
                  </el-form-item>
                  <span>电话:</span>
                  <el-form-item style="display: inline-block;width: 250px;margin-left: 10px" class="rules"  :prop="'moreNotifyObject.'+ index +'.userphone'"  :rules="rules.phone">
                    <el-input v-model="item.userphone" placeholder="请输入电话号码"  class="el-select_box"></el-input>
                  </el-form-item>
                  <span style="margin-left: 5px">成交价格:</span>
                  <el-form-item style="display: inline-block;width: 100px;margin-left: 10px" class="rules"  :prop="'moreNotifyObject.'+ index +'.price'" >
                    <el-input  v-model="item.price" type="number" class="el-select_box" @keyup.native="proving1($event)"></el-input>
                  </el-form-item>
                  <span style="margin-left: 5px">备注信息:</span>
                  <el-form-item style="display: inline-block;width: 200px;margin-left: 10px" class="rules"  :prop="'moreNotifyObject.'+ index +'.beizhu'">
                  <el-input v-model="item.beizhu"   class="el-select_box"></el-input>
                </el-form-item>
                  <el-button @click="deleteRules(item)">删除</el-button>
                </div>
                <el-form-item >
                  <el-button type="primary" class="addUser" @click="addUser" >添加用户</el-button>
                </el-form-item>
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click.native="okey">确 定</el-button>
              </div>
                <div>
                  <el-dialog :style="{}" :modal="false"  title="" :visible.sync="dialogTableVisible2">
                    <!--<el-table :style="{}"  :data="this.ruleform.moreNotifyObject">-->
                      <!--<el-table-column property="username" label="姓名" width="150"></el-table-column>-->
                      <!--<el-table-column type="number" property="price"  label="价格/元"></el-table-column>-->
                    <!--</el-table>-->
                    <div style="margin-bottom: 60px">
                      <span style="font-size: 20px;font-weight: 400">用户信息确认无误后点击提交</span>
                    </div>
                    <div>
                      <span  style="font-size: 30px; font-weight: 300;margin-top: 20px">总计{{myarrlenght}}人</span>
                      <span style="font-size: 30px; font-weight: 300;margin-top: 20px">总价{{totalPrice}}元</span>
                    </div>
                    <div style="margin-top: 50px">
                      <el-button @click="dialogTableVisible2 = false">取 消</el-button>
                      <el-button type="primary" @click="submit">提交</el-button>
                    </div>
                  </el-dialog>

                </div>
            </el-form>
            <div style="font-size: 20px; font-weight: 300; margin-top: 50px">
                总计{{totalPrice}}元
            </div>
          </el-dialog>
        </div>

      </el-dialog>
    </div>
    <!--日期  状态 查询-->
    <div style="margin-top: 20px">
      <div class="block" style="display: inline-block">
        <span class="demonstration">起始时间:</span>
        <el-date-picker
          v-model="value2"
          value-format="yyyy-MM-dd"
          align="right"
          type="date"
          placeholder="开始时间"
          :picker-options="pickerOptions1">
        </el-date-picker>
      </div>
      <div class="block" style="display: inline-block">
        <span class="demonstration">结束时间:</span>
        <el-date-picker
          v-model="value3"
          value-format="yyyy-MM-dd"
          align="right"
          type="date"
          placeholder="结束时间"
          :picker-options="pickerOptions1">
        </el-date-picker>
      </div>

      <div style="display: inline-block">
        <span>状态:</span>
        <el-select v-model="selectvalue" placeholder="请选择">
          <el-option
            v-for="item in orderStatus"
            :key="item.value"
            :label="item.keyValue"
            :value="item.keyId">
          </el-option>
        </el-select>
      </div>
      <el-button @click="seebtn" type="primary">查询订单</el-button>
    </div>
    <div style="margin-top: 20px"><el-table
        :default-sort = "{prop: 'begin_date'}"
        :data="this.orderLists"
        style="width: 100%;"
        :row-class-name="tableRowClassName">
      <el-table-column
        style="margin: 0 auto"
        property="create_date"
        label="创建时间"
        width="180">
      </el-table-column>
        <el-table-column
          style="margin: 0 auto"
          property="course_name"
          sortable
          label="课程名称"
          width="180">
        </el-table-column>
        <el-table-column
          property="begin_date"
          prop="begin_date"
          label="开始时间"
          sortable
          width="180">
        </el-table-column>
        <el-table-column
          property="end_date"
          label="结束时间"
          width="180">
        </el-table-column>
        <el-table-column
          property="total_customers"
          label="人数"
          width="180">
        </el-table-column>
        <el-table-column
          property="total_amount"
          label="总价"
          width="180">
        </el-table-column>
      <!--<el-table-column-->
        <!--property="sts"-->
        <!--label="状态"-->
        <!--width="100"-->
        <!--filter-placement="bottom-end">-->
        <!--<template slot-scope="scope">-->
          <!--<el-tag-->
            <!--:type="scope.row.sts === '不付款' ? 'primary' : 'success'"-->
            <!--disable-transitions>{{scope.row.sts}}</el-tag>-->
        <!--</template>-->
      <!--</el-table-column>-->
        <el-table-column
          property="sts"
          label="状态"
          width="180">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)">{{scope.row.sts}}</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  import common from '../../../api/common'
  import axios from 'axios'
  export default {
    data() {
      var checkPhone = (rule, value, callback) => {
        if (!value)
        {
          return callback(new Error('手机号不能为空'))
        } else {
          const reg = /^1[3-9]{1}\d{9}$/;
          this.reguserphone  = reg.test(value)
          // console.log(reg.test(value));
          if (reg.test(value)) {
            callback();
          } else {
            return callback(new Error('请输入正确的手机号'));
          }
        }
      };
      return {
        //状态下拉菜单值
        selectvalue:"",
        //起止日期 结束日期
        time:'',
        //日期操作
        pickerOptions1: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        //起始日期
        value2: '',
        //结束日期
        value3:'',
        //起止日期  结束日期
        begindata:'',
        enddata:'',
        startdata:'',
        lastdata:'',
        //正则
        regusername:null,
        reguserphone:null,
        pattern : new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>/?~！@#￥……&*（）——|{}【】‘；：”“'。，、？]"),
        //提交数据参数
        object:{
        agentOrder:{
          totalAmount:0,
          courseId:'',
        },
        userSession:{
          'userSessionId':sessionStorage.getItem('sessionID')
        },
        orderDetails:[
          {
            customer:{
              customerName:'',
              customerMobile:'',
              customerMemoInf:''
            },
            acturalPrice:'',
            standardPrice:''
          }
        ],
      },
        //查询课程信息
        seeclassObj:{
          userSession:{
            'userSessionId':sessionStorage.getItem('sessionID')
          },
          startDate: " ",
          endDate: " ",
          orderStatus:''
        },
        //课程接口
        agentobj:{
          userSession:{
            'userSessionId':sessionStorage.getItem('sessionID')
          },
        },
        //请求接口路径
        agentOrderQueryurl:common.agentOrderQueryurl,
        getOrderQueryurl:common.getOrderQueryurl,
        agentenrollurl:common.agentenrollurl,
        agentcreaturl:common.agentcreaturl,
        //增加表单的数组
        ruleform:{
        price:0,
        moreNotifyObject: [{
          username: '',
          userphone: '',
          price:'',
          beizhu:'',
        }]
      },
        rules: {
          phone: [
            {validator: checkPhone, trigger: 'blur'}
          ],
          userName: [{ required: true, message: '请输入姓名', trigger: 'blur' },
              {
                validator: (rule, value, callback) => {
                  if (value.length > 15 || value.length < 2) {
                    callback(new Error('长度必须为2~8个字符'))
                  } else {
                    var reg = new RegExp("[`~!@#$^&*()=|{}':',\\[\\].<>《》/?~！@#￥……&*（）——|{}【】‘；：”“'。，、？]")
                    this.regusername = reg.test(value)
                    if (reg.test(value)) {
                      callback(new Error('不能含有特殊字符'))
                    } else {
                      callback()
                    }
                  }
                },
                trigger: 'change'
              }
            ],
        },
        //循环增加的表单
        dynamicValidateForm: {
          domains: [{
            value: '',
          }],
        },
        //创建订单 弹出窗口开关
        dialogFormVisible2: false,
        dialogTableVisible2: false,
        dialogFormVisible: false,
        dialogTableVisible: false,
        //选择课程信息对象
        currentRow: null,
        //网络请求对象

        //课程接口
        title:'',
        myprice:'',
        formLabelWidth: '50px',
        multis:{},
        //数据数组
        course:[],
        multipleSelection: [],
        //参数数组中couseid
        mycourseId:'',
        myarrlenght:'',
        //状态内容
        orderStatus:[],
        //查询页面
        orderLists:[],
        //按钮状态
        buttonstate:''
      }
    },
    created(){
      //请求状态接口
      axios.post(this.getOrderQueryurl,this.agentobj).then(response=>{
        // console.log(response)
        this.orderStatus = response.data.data.orderStatus
      })
      //请求课程列表接口
      axios.post(this.agentenrollurl,this.agentobj).then(response=>{
        // console.log(response)
        this.course = response.data.data.course;
        for (var i = 0;i<response.data.data.course.length;i++){
          //处理日期字符串
          response.data.data.course[i]['begin_date'] = response.data.data.course[i]['begin_date'].substr(0,9);
          response.data.data.course[i]['end_date'] = response.data.data.course[i]['end_date'].substr(0,9)
        }
      }).catch({
      })
    },
    methods: {
      //查询按钮
      seebtn(){
        this.seeclassObj.startDate = this.value2
        this.seeclassObj.endDate = this.value3
        this.seeclassObj.orderStatus = this.selectvalue.toString()
        // console.log(this.seeclassObj)
        // console.log(this.value2)
        // console.log(this.value3)
        axios.post(this.agentOrderQueryurl,this.seeclassObj).then(response=>{
          this.orderLists  = response.data.data.orderLists
          console.log(response.data.data.orderLists)
          for (var i = 0;i<response.data.data.orderLists.length;i++){
            //处理日期字符串
            response.data.data.orderLists[i]['begin_date'] = response.data.data.orderLists[i]['begin_date'].substr(0,9);
            response.data.data.orderLists[i]['end_date'] = response.data.data.orderLists[i]['end_date'].substr(0,9)
            response.data.data.orderLists[i]['create_date'] = response.data.data.orderLists[i]['create_date'].substr(0,9)
          }
          //
        }).catch(err => {

        });
      },
      //下一步
      nexthome(){
        // console.log(this.ruleform.price)
        if (this.currentRow!==null){
          this.$message.success('欢迎报名');
          for (var i = 0; i<this.ruleform.moreNotifyObject.length;i++){
            this.ruleform.moreNotifyObject[i].price = this.ruleform.price.toString()
          }
          this.dialogFormVisible=true
        } else {
          this.$message.error('请选择一门课程')
        }
      },
      proving1(e){
        var keynum = window.event ?e.keyCode :e.which//获取键盘ma~
        var keychar = String.fromCharCode(keynum);
        if (keynum ===189 || keynum === 190 || keynum === 110 || keynum ===109) {
          this.$message.warning("禁止输入小数以及负数")
          e.target.value = ''
        }
      },
      handleCurrentChange(val) {

        this.currentRow = val;
        this.title = this.currentRow.course_name;
        this.ruleform.price = this.currentRow.price;
        this.myprice = this.currentRow.price;
        this.mycourseId=this.currentRow.course_id
        // console.log(this.currentRow.course_id)


      },
      //点击添加用户
      addUser() {
        // console.log(this.ruleform.moreNotifyObject.length)
        // console.log(this.object.orderDetails.length)
        this.ruleform.moreNotifyObject.push({
          username: '',
          userphone: '',
          price:'',
          beizhu:''
        });
        this.object.orderDetails.push({
          customer:{
            customerName:'',
            customerMobile:'',
            customerMemoInf:''
          },
          acturalPrice:'',
          standardPrice:''
        })

      },
      //删除用户
      deleteRules(item,) {
        // console.log(this.object.orderDetails.length)
        // console.log(this.ruleform.moreNotifyObject.length)
        var index = this.ruleform.moreNotifyObject.indexOf(item);
        if (index !== -1) {
          this.ruleform.moreNotifyObject.splice(index, 1)
          this.object.orderDetails.splice(index,1)
        }

      },
      //提交用户信息
      okey(){
          // console.log(this.myarrlenght);
        this.myarrlenght = this.ruleform.moreNotifyObject.length;
        //赋值
        for (var i=0; i<this.ruleform.moreNotifyObject.length; i++) {
          if (this.ruleform.moreNotifyObject[i].username === ''){
            this.$message.error("姓名不能为空");
            this.dialogTableVisible2 = false
          }else if(this.ruleform.moreNotifyObject[i].userphone===''){
            this.$message.error('手机号码不能为空');
            this.dialogTableVisible2 = false
          }else if(this.ruleform.moreNotifyObject[i].price > this.ruleform.price){
            this.$message.error('价格不得超过标准价不准有特殊符号');
            this.dialogTableVisible2 = false
          }else if (this.ruleform.moreNotifyObject[i].price === '' ){
            this.$message.error('价格是必填项');
            this.dialogTableVisible2 = false
          }
          else if (this.pattern.test(this.ruleform.moreNotifyObject[i].price)){
            this.$message.error('价格不允许有特殊符号');
            this.dialogTableVisible2 = false
          }else if (this.reguserphone ===false){
            this.$message.error('手机号码格式不正确');
            this.dialogTableVisible2 = false
          }else if(this.regusername === true){
            this.$message.error('姓名格式不正确')
          }
          else  {
            this.dialogTableVisible2 = true
          }
          for (var j = 0; j < this.ruleform.moreNotifyObject.length;j++){
            if (i!==j&&this.ruleform.moreNotifyObject[i].userphone === this.ruleform.moreNotifyObject[j].userphone){
              this.$message.error('手机号码不得重复');
              this.dialogTableVisible2 = false
            }
          }
        }

        // console.log(this.ruleform.moreNotifyObject.price)
      },
      //提交用户课程信息
      submit(){
        // console.log(this.currentRow.course_id)
        this.object.agentOrder.totalAmount = this.totalPrice.toString();
        this.object.agentOrder.courseId = this.currentRow.course_id
        for (var i = 0; i<this.ruleform.moreNotifyObject.length;i++){
          // this.ruleform.moreNotifyObject[i].price = this.ruleform.price
          this.object.orderDetails[i].customer.customerName = this.ruleform.moreNotifyObject[i].username;
          this.object.orderDetails[i].customer.customerMobile = this.ruleform.moreNotifyObject[i].userphone;
          this.object.orderDetails[i].customer.customerMemoInf = this.ruleform.moreNotifyObject[i].beizhu;
          this.object.orderDetails[i].acturalPrice = this.ruleform.moreNotifyObject[i].price;
          this.object.orderDetails[i].standardPrice = this.myprice.toString()
        }
          // console.log(this.object);

      //请求接口
        axios.post(this.agentcreaturl,this.object).then(response=>{
          console.log(response);
          if (response.data.flag !==0){
            this.$message.error(response.data.message)
          }else {
            // localStorage.setItem('begindata',this.currentRow.begin_date)
            // localStorage.setItem('enddata',this.currentRow.end_date)
            // localStorage.setItem('startdata','起始日期')
            // localStorage.setItem('lastdata','结束日期')
            this.$message.success('提交成功')
            // this.begindata = this.currentRow.begin_date
            // this.enddata = this.currentRow.end_date
            // this.startdata = '起始日期'
            // this.lastdata = '结束日期'
            this.dialogFormVisible2= false,
            this.dialogTableVisible2=false,
            this.dialogFormVisible=false,
            this. dialogTableVisible=false
          }
        }).catch(err => {

        });
        // this.dialogTableVisible2 = false
        // console.log(this.orderDetails[0].courseId)
        // console.log(this.mycourseId)
        // console.log(this.currentRow.course_id)
        // console.log(this.ruleform.price)
        // console.log(sessionStorage.getItem('sessionID'));
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex  === 1) {
          return 'warning-row';
        } else if (rowIndex === 3) {
          return 'success-row';
        }
        return '';
      },
      //查询列表点击按钮
      handleEdit(index, row) {
        console.log(index, row);
      },
    },
    //计算总价
    computed: {
      totalPrice: function () {
        var allprice =0
        for(var i = 0; i < this.ruleform.moreNotifyObject.length; i++){
        allprice += this.ruleform.moreNotifyObject[i].price*1
        }
        return allprice
      }
    },
  }
</script>

<style>

  .el-table--striped .el-table__body tr.el-table__row--striped.current-row td,
  .el-table__body tr.current-row>td {
    background-color: #ffec8b;
  }
  #el-price{
    color: red;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
</style>



