import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home/Home'
import login from '@/views/login/login'
import show from '@/views/Home/children/show'
import firstpage from '@/views/Home/children/firstpage'
import agent_enroll from '@/views/Home/children/agent_enroll'
import agent_apply_fee from '@/views/Home/children/agent_enroll'
import agent_confirm_fee from '@/views/Home/children/agent_confirm_fee'
import agent_pay from '@/views/Home/children/agent_pay'
import cust_purchase from '@/views/Home/children/cust_purchase'
import cust_sign_in from '@/views/Home/children/cust_sign_in'
import financial_config from '@/views/Home/children/financial_config'
import financial_pay from '@/views/Home/children/financial_pay'
Vue.use(Router);

 const router = new Router({
   mode : 'history',
   base: '/dist/',
   routes: [
    {
      path: '/Home',
      name: 'Home',
      component: Home,
      redirect: '/Home/agent_enroll',
      children:[
        {
          path:'show',
          component:show
        },
        {
          path:'firstpage',
          component:firstpage
        },
        {
          path:'agent_enroll',
          component:agent_enroll
        },
        {
          path:'agent_apply_fee',
          component:agent_apply_fee
        },
        {
          path:'agent_confirm_fee',
          component:agent_confirm_fee
        },
        {
          path:'agent_enroll',
          component:agent_enroll
        },
        {
          path:'agent_pay',
          component:agent_pay
        },
        {
          path:'cust_purchase',
          component:cust_purchase
        },
        {
          path:'cust_sign_in',
          component:cust_sign_in
        },
        {
          path:'financial_config',
          component:financial_config
        },
        {
          path:'financial_pay',
          component:financial_pay
        },


      ],
      meta: {
        requireAuth:true, // 添加该字段，表示进入这个路由是需要登录的
      },
    },
    {
      path:'/',
      name:"login",
      component:login
    },
    {
      path: '**',   // 错误路由
      redirect: '/Home'   //重定向
    },

  ]
});
//判断是否登录
// router.beforeEach((to, from, next) => {
//   if (to.meta.requireAuth) {   //如果需要跳转 ，往下走（1）
//     if (localStorage.getItem('sessionID')!==null) {   //判断是否登录过，如果有登陆过，说明有token,或者token未过期，可以跳过登录（2）
//       if (to.path === '/login') { //判断下一个路由是否为要验证的路由（3）
//         next('/index');   // 如果是直接跳到首页，
//       } else {    //如果该路由不需要验证，那么直接往后走
//         next();
//       }4
//     } else {
//       console.log('没有');  //如果没有登陆过，或者token 过期， 那么跳转到登录页
//       next('/login');
//     }
//   } else {       //不需要跳转，直接往下走
//     next();
//   }
// })

export default router



