<template>
  <div class="loginwrap">
    <el-form class="login-form" autoComplete="on" :model="loginForm"  ref="loginForm" label-position="left">
      <div class="title-container">
        <h3 class="title">爱能商贸管理系统</h3>
      </div>
      <el-form-item prop="username">
        <span class="svg-container svg-container_login">

        </span>
        <el-input style="width:17.2%" name="username" type="text" v-model="loginForm.loginId" autoComplete="on" placeholder="用户名" />
      </el-form-item>
      <el-form-item prop="password">
        <span class="svg-container">
        </span>
        <el-input style="width:17.2%" name="password"  type="password"  @keyup.enter.native="handleLogin" v-model="loginForm.password" autoComplete="on" placeholder="密码" />
        <span class="show-pwd">
      </span>
      </el-form-item>
      <div style="margin-top: -10px">
        <el-checkbox v-model="checked">记住账号</el-checkbox>
      </div>
      <el-button  type="primary" style="margin-top:10px;width:17.2%;margin-bottom:30px;" :loading="loading" @click="handleLogin">登录</el-button>
    </el-form>
  </div>
</template>
<script>
  import common from '../../api/common'
  import axios from 'axios'
  // import wxpay from '../api/wx'
  export default {
    data() {

      // const validateUsername = (rule, value, callback) => {
      //   if (validateUsername == null) {
      //     callback(new Error('请输入正确的用户名'))
      //   } else {
      //     callback()
      //   }
      // };
      // const validatePassword = (rule, value, callback) => {
      //   if (value.length < 6) {
      //     callback(new Error('管理员密码长度应大于6'))
      //   } else {
      //     callback()
      //   }
      // };
      return {
        checked: true,
        loginUrl:common.loginUrl,
        loginForm:{
          'loginId':"",
          'password':null
        },
        // loginRules: {
        //   username: [{ required: true, trigger: 'blur', validator: validateUsername }],
        //   password: [{ required: true, trigger: 'blur', validator: validatePassword }]
        // }
        loading: false
      }
    },
    created(){

      //判断用户浏览器是否支持
      sessionStorage.setItem('user',"123");
      if (sessionStorage.getItem('user')!=="123"){
        this.$message.error('当前浏览器不支持，请更换浏览器')
      }
      if (localStorage.getItem('username')){
        this.loginForm.loginId =  localStorage.getItem('username')
      }


    },
    methods: {
      handleLogin(){
        if (this.checked ===true ){
          localStorage.setItem('username',this.loginForm.loginId)
        }
        sessionStorage.setItem('user',"123");
        if (sessionStorage.getItem('user')!=="123"){
          this.$message.error('当前浏览器不支持，请更换浏览器')
        }else {
          this.loading = true;
          // console.log(this.loginForm);
          // console.log(this.loginUrl)
          axios.post(this.loginUrl,this.loginForm)
            .then(response => {
              // console.log(response)
              //储存整个数据 只能储存字符串
              sessionStorage.setItem('menuInf',JSON.stringify(response.data.data.menuInf));
              let newarr=JSON.parse(sessionStorage.getItem('menuInf'));
              // console.log(newarr)
              this.$store.state.menuInf=newarr;
              // console.log(response.data);
              //判断flag值 为0成功
              if (response.data.flag !==0){
                this.$message('账号或密码错误');
                this.loading=false
              }else{
                this.$message({
                  message: "登陆成功",
                  type:'success'
                });
                //跳转至首页
                this.loading=false;
                // this.$store.state.uid=this.loginForm.loginId
                // this.$store.state.upd=this.loginForm.password
                sessionStorage.setItem('sessionID', response.data.data.sessionID);
                this.$router.push({ name: 'Home'})
              }
            })
            .catch(err => {
              this.loading=false;
              this.$message.error('账号或密码错误');
            });
        }
      },
    },
    destroyed(){

    }
  }
</script>

<style scoped>
  .loginwrap{
    margin-top: 0;
    height: 730px;
    background-color: #2d3a4b;
    text-align: center;
    padding-top: 200px;

  }
  .title-container{
    font-family: 微软雅黑;
    font-size: 30px;
    color: white;
    margin-bottom: 30px;
  }
  .login-form{
  }

</style>



